@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card{
	margin: 10px;
	overflow: hidden;
	text-decoration: none;
}

.card:hover{
	box-shadow: 0px 8px 8px 0px gray;
}
.card-title{
	font-size: 2vw;
	text-shadow: 2px 2px pink;
	color: black;
	text-align: center;
}

.card-text, .text-muted{
	font-size: 1vw;
	color:  black;
}

.card-group{
	margin: 20px;
	justify-content: center;
}

#aboutMeCards1{
	background-image: url(allenzhai.github.io./static/media/candid.1b044063.jpg);
	background-size: cover;
	min-height: 60vh;
	margin: 0px;
	box-shadow: 0px 8px 8px 0px gray;
}
#aboutMeCards2{
	margin: 0px;
	box-shadow: 0px 8px 8px 0px gray;
}

.card-img{
	-webkit-transition: all .5s ease;
	transition: all .5s ease;
	width: 100%;
    height: 25vw;
	object-fit: cover;
	-webkit-filter: blur(55px);
	        filter: blur(55px);
}
.card-img-overlay{
	-webkit-transition: all .5s ease;
	transition: all .5s ease;
	object-fit: cover;
    height: 25vw;
	background-color: rgba(255,255,255,.2);
	-webkit-filter: blur(0px);
	        filter: blur(0px);
}

.card:hover .card-img {
	-webkit-filter: blur(0px);
	        filter: blur(0px);
}

.card:hover .card-img-overlay {
	background-color: rgba(255,255,255,0);
	-webkit-filter: blur(15px);
	        filter: blur(15px);
}

.all{
    font-family: 'Montserrat', sans-serif;
}

.header {

  background-color: #282c34;
  background-image: url(allenzhai.github.io./static/media/header_wallpaper.b1e5a3e1.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.projects{
     min-height: 3000vh;
}

/* Welcome */
.pStyle1 {
    text-align: center;
    font-size: 50px;
    font-weight: bold;
    margin: 10px;
    text-shadow: 2px 2px pink;
}

/* I'm */
.pStyle2 {
    text-align: center;
    font-size: 35px;
    margin: 10px;
}

/* Allen */
.pStyle3 {
    text-align: center;
    font-size: 75px;
    font-weight: bold;
    margin: 10px;
