    margin: 1vw;
}

.pStyle5 {
    font-size: 15px;
    margin: 1vw;
}

.container {
    box-shadow: 0px 1px 1px 0px gray;
}

.resume {
    background-color: pink;
    box-shadow: 0px 2px 2px 0px gray;
    text-align: center;
    text-decoration: none;
    color: black;
    font-size: 20px;
    object-position: center;
    border-radius: 5px;
    padding: 5px;
}

.resume:hover {
    text-decoration: none;
    box-shadow: 0px 1px 1px 0px gray;
    color: black;
}

.resumeContainer{
    width : 100vw;
    height : 100vh;
}

.btn {
    width: 100%;
    text-align:center;
}

.row1{
    background-color: #FAFAFA;
    min-height: 100vh;
    margin: 20px;
}

.foot{
    text-align: center;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 4%;
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
}

.icon{
    margin: 0px 20px 0px 20px;
}

.icon-img{
    height: 20px;
    width: 20px;
    object-fit: contain;
}

.profile {
    object-fit: cover;
    align-self: center;
    height: 300px;
    width: 300px
}

